import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'


export default function Navbar() {
    var [search, setSearch] = useState("")
    var navigate = useNavigate()
    function postSearch(e) {
        e.preventDefault()
        navigate({
            pathname: '/shop/All/All/All',
            search: '?search=' + search
        });
    }
    function logout() {
        localStorage.clear()
        navigate("/login")
    }
    return (
        <>
            <div className="container-fluid">
                <div className="row align-items-center bg-light py-3 px-xl-5 d-none d-lg-flex">
                    <div className="col-lg-4">
                        <Link to="/" className="text-decoration-none">
                            <span className="h1 text-uppercase text-primary bg-dark px-2">Student</span>
                            <span className="h1 text-uppercase text-dark bg-primary px-2 ml-n1">Mall</span>
                        </Link>
                    </div>
                    <div className="col-lg-4 col-6 text-left">
                        <form onSubmit={postSearch}>
                            <div className="input-group">
                                <input type="text" name='search' onChange={(e) => setSearch(e.target.value)} className="form-control" placeholder="Search for products" />
                                <div className="input-group-append">
                                    <button className="input-group-text bg-transparent text-primary">
                                        <i className="fa fa-search"></i>
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div className="col-lg-4 col-6 text-right">
                        <p className="m-0">Customer Service</p>
                        <h5 className="m-0">8433208146</h5>
                    </div>
                </div>
            </div>

            <div className="container-fluid bg-dark mb-30 sticky-top">
                <div className="row px-xl-5">
                    <div className="col-lg-12">
                        <nav className="navbar navbar-expand-lg bg-dark navbar-dark py-3 py-lg-0 px-0">
                            <Link to="/" className="text-decoration-none d-block d-lg-none">
                                <span className="h1 text-uppercase text-dark bg-light px-2">Student</span>
                                <span className="h1 text-uppercase text-light bg-primary px-2 ml-n1">Mall</span>
                            </Link>
                            <button type="button" className="navbar-toggler" data-toggle="collapse" data-target="#navbarCollapse">
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="collapse navbar-collapse justify-content-between" id="navbarCollapse">
                                <div className="navbar-nav mr-auto py-0">
                                    <Link to="/" className="nav-item nav-link active">Home</Link>
                                    <Link to="/shop/All/All/All" className="nav-item nav-link">Shop</Link>
                                    <Link to="/contact" className="nav-item nav-link">Contact</Link>
                                </div>
                                <div className="navbar-nav ml-auto py-0">
                                    {
                                        localStorage.getItem("login") ?
                                            <div className="nav-item dropdown">
                                                <a href="#" className="nav-link dropdown-toggle" data-toggle="dropdown">{localStorage.getItem("name")} <i className="fa fa-angle-down mt-1"></i></a>
                                                <div className="dropdown-menu bg-primary rounded-0 border-0 m-0">
                                                    {
                                                        localStorage.getItem("role") === "Admin" ?
                                                            <>
                                                                <Link to="/admin" className="dropdown-item">Profile</Link>
                                                            </> :
                                                            <>
                                                                <Link to="/profile" className="dropdown-item">Profile</Link>
                                                                <Link to="/cart" className="dropdown-item">Cart</Link>
                                                            </>
                                                    }
                                                    <button className="dropdown-item" onClick={logout}>Logout</button>
                                                </div>
                                            </div> :
                                            <Link to="/login" className="nav-item nav-link">Login</Link>
                                    }
                                </div>
                            </div>
                        </nav>
                    </div>
                </div>
            </div>
        </>
    )
}
