import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'


export default function UpdateProfile() {
    var [data, setData] = useState({
        name: "",
        pic: "",
        email: "",
        phone: "",
        addressline1: "",
        addressline2: "",
        addressline3: "",
        pin: "",
        city: "",
        state: ""
    })
    var navigate = useNavigate()
    function getInputData(e) {
        var { name, value } = e.target
        setData((old) => {
            return {
                ...old,
                [name]: value
            }
        })
    }
    function getInputFile(e) {
        var { name, files } = e.target
        setData((old) => {
            return {
                ...old,
                [name]: files[0]
            }
        })
    }
    async function postData(e) {
        e.preventDefault()
        var formData = new FormData()
        formData.append("name", data.name)
        formData.append("email", data.email)
        formData.append("phone", data.phone)
        formData.append("addressline1", data.addressline1)
        formData.append("addressline2", data.addressline2)
        formData.append("addressline3", data.addressline3)
        formData.append("pin", data.pin)
        formData.append("city", data.city)
        formData.append("state", data.state)
        formData.append("pic", data.pic)
        var response = await fetch("/api/user/" + data._id, {
            method: "put",
            headers: {
                "authorization": localStorage.getItem("token")
            },
            body: formData
        })
        response = await response.json()
        if (response.result === "Done") {
            if (data.role === "Admin")
                navigate("/admin")
            else
                navigate("/profile")
        }
        else
            alert(response.message)
    }
    async function getAPIData() {
        var response = await fetch("/api/user/" + localStorage.getItem("userid"), {
            method: "get",
            headers: {
                "content-type": "application/json",
                "authorization": localStorage.getItem("token")
            }
        })
        response = await response.json()
        if (response.result === "Done")
            setData(response.data)
        else
            navigate("/login")
    }
    useEffect(() => {
        getAPIData()
    }, [])
    return (
        <div className="container-fluid my-3">
            <div className="w-75 m-auto">
                <h5 className='bg-primary text-center p-2'><span className='text-info'>Update</span> Your Profile</h5>
                <form onSubmit={postData}>
                    <div className="row mb-3">
                        <div className="col-md-6">
                            <label>Name</label>
                            <input type="text" value={data.name} onChange={getInputData} name="name" placeholder='Enter Full Name' className='form-control' />
                        </div>
                        <div className="col-md-6">
                            <label>Pic</label>
                            <input type="file" onChange={getInputFile} name="pic" className='form-control' />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-6">
                            <label>Email</label>
                            <input type="email" value={data.email} onChange={getInputData} name="email" placeholder='Enter Email Address' className='form-control' />
                        </div>
                        <div className="col-md-6">
                            <label>Phone</label>
                            <input type="text" value={data.phone} onChange={getInputData} name="phone" placeholder='Enter Phone Number' className='form-control' />
                        </div>
                    </div>

                    <div className="row mb-3">
                        <div className="col-md-6">
                            <label>Address Line 1</label>
                            <input type="text" value={data.addressline1} onChange={getInputData} name="addressline1" placeholder='Enter House Number or Building Number' className='form-control' />
                        </div>
                        <div className="col-md-6">
                            <label>Address Line 2</label>
                            <input type="text" value={data.addressline2} onChange={getInputData} name="addressline2" placeholder='Enter Village or Near By' className='form-control' />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <div className="col-md-6">
                            <label>Address Line 3</label>
                            <input type="text" value={data.addressline3} onChange={getInputData} name="addressline3" placeholder='Enter Street Number or Near By' className='form-control' />
                        </div>
                        <div className="col-md-6">
                            <label>Pin Code</label>
                            <input type="number" value={data.pin} onChange={getInputData} name="pin" placeholder='Enter PIN Code' className='form-control' />
                        </div>
                    </div>
                    <div className="row mb-3">
                        <div className="col-md-6">
                            <label>City</label>
                            <input type="text" value={data.city} onChange={getInputData} name="city" placeholder='Enter City' className='form-control' />
                        </div>
                        <div className="col-md-6">
                            <label>State</label>
                            <input type="text" value={data.state} onChange={getInputData} name="state" placeholder='Enter State' className='form-control' />
                        </div>
                    </div>
                    <div className="mb-3">
                        <button type="submit" className='btn btn-primary w-100'>Update</button>
                    </div>
                </form>
            </div>
        </div>
    )
}
