import React, { useState, useEffect } from 'react'
import { Link, useNavigate } from 'react-router-dom'

import { getWishlist, deleteWishlist } from "../Store/ActionCreators/WishlistActionCreators"
import { getCheckoutUser } from "../Store/ActionCreators/CheckoutActionCreators"
import { useDispatch, useSelector } from 'react-redux'
import BuyerProfile from './BuyerProfile'

export default function Profile() {
  var [user, setUser] = useState({})
  var [wishlist, setWishlist] = useState([])
  var [order, setOrder] = useState([])

  var allWishlists = useSelector(state => state.WishlistStateData)
  var allCheckouts = useSelector(state => state.CheckoutStateData)

  var dispatch = useDispatch()
  var navigate = useNavigate()
  async function getAPIData() {
    var response = await fetch("/api/user/" + localStorage.getItem("userid"), {
      method: "get",
      headers: {
        "content-type": "application/json",
        "authorization": localStorage.getItem("token")
      }
    })
    response = await response.json()
    if (response.result === "Done")
      setUser(response.data)
    else
      navigate("/login")

    dispatch(getWishlist())
    dispatch(getCheckoutUser())
    if (allWishlists.length) {
      setWishlist(allWishlists)
    }
    if (allCheckouts.length) {
      setOrder(allCheckouts.filter((x) => x.userid === localStorage.getItem("userid")))
    }
  }
  function deleteItem(_id) {
    dispatch(deleteWishlist({ _id: _id }))
  }
  useEffect(() => {
    getAPIData()
  }, [allWishlists.length, allCheckouts.length])
  return (
    <div className='container-fluid my-3'>
      <div className="row">
        <div className="col-md-6">
          {
            user.pic ?
              <img src={`/public/users/${user.pic}`} height="600px" width="100%" alt="" /> :
              <img src={`/assets/img/noimage.png`} height="600px" width="100%" alt="" />
          }
        </div>
        <div className="col-md-6">
          <h5 className='text-center bg-primary p-2'>My Profile</h5>
          <BuyerProfile user={user} />

        </div>
      </div>
      <h5 className='mt-2 text-center bg-primary p-2'>Wishlists</h5>
      <div className="table-responsive">
        {
          wishlist.length ?
            <table className='table table-bordered'>
              <tbody>
                <tr>
                  <th></th>
                  <th>Name</th>
                  <th>Brand</th>
                  <th>Color</th>
                  <th>Size</th>
                  <th>Price</th>
                  <th></th>
                  <th></th>
                </tr>
                {
                  wishlist.map((item, index) => {
                    return <tr key={index}>
                      <td><a target='_blank' href={`/public/products/${item.pic}`}>
                        <img src={`/public/products/${item.pic}`} height="100px" width="100%" className='rounded' alt="" />
                      </a></td>
                      <td>{item.name}</td>
                      <td>{item.brand}</td>
                      <td>{item.color}</td>
                      <td>{item.size}</td>
                      <td>&#8377;{item.price}</td>
                      <td><Link to={`/single-product/${item.productid}`}><i className='fa fa-shopping-cart text-success'></i></Link></td>
                      <td><button className='btn' onClick={() => deleteItem(item._id)}><i className='fa fa-trash text-danger'></i></button></td>
                    </tr>
                  })
                }
              </tbody>
            </table> :
            <div className='text-center'>
              No Items in Wishlist
            </div>
        }

        <h5 className='mt-2 bg-primary p-2 text-center'>Order History</h5>
        {
          order.length ?
            order.map((item, index) => {
              return <div key={index} className="row">
                <div className="col-lg-4">
                  <div className="table-responsive">
                    <table className="table table-sm table-bordered">
                      <tbody>
                        <tr>
                          <th>Order Id</th>
                          <td>{item.id}</td>
                        </tr>
                        <tr>
                          <th>Order Status</th>
                          <td>{item.orderstatus}</td>
                        </tr>
                        <tr>
                          <th>Payment Mode</th>
                          <td>{item.paymentmode}</td>
                        </tr>
                        <tr>
                          <th>Payment Status</th>
                          <td>{item.paymentstatus}
                            {
                              item.paymentmode==="Net Banking" && item.paymentstatus==="Pending"?
                                <Link to={`/payment/${item._id}`} className='btn btn-primary' >Pay Again</Link>:""
                          }
                          </td>
                        </tr>
                        <tr>
                          <th>Subtotal</th>
                          <td>&#8377;{item.subtotal}</td>
                        </tr>
                        <tr>
                          <th>Shipping</th>
                          <td>&#8377;{item.shipping}</td>
                        </tr>
                        <tr>
                          <th>Total</th>
                          <td>&#8377;{item.total}</td>
                        </tr>
                        <tr>
                          <th>Date</th>
                          <td>&#8377;{item.date}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
                <div className="col-lg-8">
                  <h6 className='text-center'>Order Products</h6>
                  <div className="table-responsive">
                    <table className='table table-sm table-bordered'>
                      <tbody>
                        <tr>
                          <th></th>
                          <th>Name</th>
                          <th>Brand</th>
                          <th>Color</th>
                          <th>Size</th>
                          <th>Price</th>
                          <th>Qty</th>
                          <th>Total</th>
                        </tr>
                        {
                          item.products.map((p, index) => {
                            return <tr key={index}>
                              <td><img src={`/public/products/${p.pic}`} height="70px" width="70px" className="rounded" alt="" /></td>
                              <td>{p.name}</td>
                              <td>{p.brand}</td>
                              <td>{p.color}</td>
                              <td>{p.size}</td>
                              <td>&#8377;{p.price}</td>
                              <td>{p.qty}</td>
                              <td>&#8377;{p.total}</td>
                            </tr>
                          })
                        }
                      </tbody>
                    </table>
                  </div>
                </div>
                <hr style={{ border: "5px solid lightgray", width: "100%" }} />
              </div>
            })
            :
            <div className='text-center'>
              No Order History Found!!!
            </div>
        }
      </div>
    </div>
  )
}
